'use client';

import { useState, useEffect } from 'react';
import { useRouter } from 'next/navigation';
import { useAuth } from '@/contexts/AuthContext';
import {
  UsersIcon, 
  ClipboardDocumentListIcon, 
  CreditCardIcon,
  DocumentCheckIcon,
  ShieldCheckIcon,
  ExclamationTriangleIcon,
  DocumentChartBarIcon,
  EnvelopeIcon
} from '@heroicons/react/24/outline';
import AdminStats from '@/components/dashboard/AdminStats';
import AdminTabContent from '@/components/dashboard/AdminTabContent';

// الألوان الخاصة بمختلف الإحصائيات
const statsColors = {
  users: 'bg-blue-500',
  providers: 'bg-purple-500',
  clients: 'bg-green-500',
  pendingVerifications: 'bg-yellow-500',
  orders: 'bg-indigo-500',
  revenue: 'bg-emerald-500',
  pendingWithdrawals: 'bg-orange-500',
  reports: 'bg-red-500',
};

// البيانات الافتراضية للعرض التوضيحي
const mockData = {
  users: { count: 1250, growth: 15, timeframe: 'الأسبوع الماضي' },
  providers: { count: 350, growth: 12, timeframe: 'الأسبوع الماضي' },
  clients: { count: 900, growth: 18, timeframe: 'الأسبوع الماضي' },
  pendingVerifications: { count: 28, growth: -5, timeframe: 'اليوم' },
  orders: { count: 780, growth: 24, timeframe: 'الشهر الحالي' },
  revenue: { count: 45600, growth: 32, timeframe: 'هذا الشهر', isCurrency: true },
  pendingWithdrawals: { count: 12, growth: 8, timeframe: 'اليوم' },
  reports: { count: 18, growth: -12, timeframe: 'هذا الأسبوع' },
};

export default function AdminDashboard() {
  const router = useRouter();
  const { user, isAdmin, loading } = useAuth();
  const [activeTab, setActiveTab] = useState('overview');
  const [isLoading, setIsLoading] = useState(true);
  const [stats, setStats] = useState(mockData);

  useEffect(() => {
    if (!loading && !isAdmin) {
      router.push('/');
    }
  }, [loading, isAdmin, router]);

  const tabItems = [
    { id: 'overview', label: 'نظرة عامة', icon: DocumentChartBarIcon },
    { id: 'users', label: 'المستخدمين', icon: UsersIcon },
    { id: 'orders', label: 'الطلبات', icon: ClipboardDocumentListIcon },
    { id: 'services', label: 'الخدمات', icon: DocumentCheckIcon },
    { id: 'payments', label: 'المدفوعات', icon: CreditCardIcon },
    { id: 'withdrawals', label: 'طلبات السحب', icon: CreditCardIcon },
    { id: 'verification', label: 'طلبات التوثيق', icon: ShieldCheckIcon },
    { id: 'reports', label: 'البلاغات', icon: ExclamationTriangleIcon },
    { id: 'messages', label: 'الرسائل', icon: EnvelopeIcon },
  ];

  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-primary-500"></div>
      </div>
    );
  }

  if (!isAdmin) {
    return null;
  }

  return (
    <div className="container mx-auto px-4 py-8">
      <h1 className="text-3xl font-bold mb-8">لوحة تحكم المشرف</h1>
      
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
        {/* إحصائيات المستخدمين */}
        <div className="bg-white rounded-lg shadow p-6">
          <h2 className="text-xl font-semibold mb-4">المستخدمين</h2>
          <div className="space-y-4">
            <div className="flex justify-between">
              <span>إجمالي المستخدمين</span>
              <span className="font-bold">0</span>
            </div>
            <div className="flex justify-between">
              <span>المزودين</span>
              <span className="font-bold">0</span>
            </div>
            <div className="flex justify-between">
              <span>العملاء</span>
              <span className="font-bold">0</span>
            </div>
          </div>
        </div>

        {/* إحصائيات الخدمات */}
        <div className="bg-white rounded-lg shadow p-6">
          <h2 className="text-xl font-semibold mb-4">الخدمات</h2>
          <div className="space-y-4">
            <div className="flex justify-between">
              <span>إجمالي الخدمات</span>
              <span className="font-bold">0</span>
            </div>
            <div className="flex justify-between">
              <span>الخدمات النشطة</span>
              <span className="font-bold">0</span>
            </div>
            <div className="flex justify-between">
              <span>الخدمات المعلقة</span>
              <span className="font-bold">0</span>
            </div>
          </div>
        </div>

        {/* إحصائيات الطلبات */}
        <div className="bg-white rounded-lg shadow p-6">
          <h2 className="text-xl font-semibold mb-4">الطلبات</h2>
          <div className="space-y-4">
            <div className="flex justify-between">
              <span>إجمالي الطلبات</span>
              <span className="font-bold">0</span>
            </div>
            <div className="flex justify-between">
              <span>الطلبات النشطة</span>
              <span className="font-bold">0</span>
            </div>
            <div className="flex justify-between">
              <span>الطلبات المكتملة</span>
              <span className="font-bold">0</span>
            </div>
          </div>
        </div>

        {/* إحصائيات المالية */}
        <div className="bg-white rounded-lg shadow p-6">
          <h2 className="text-xl font-semibold mb-4">المالية</h2>
          <div className="space-y-4">
            <div className="flex justify-between">
              <span>إجمالي الإيرادات</span>
              <span className="font-bold">0 ريال</span>
            </div>
            <div className="flex justify-between">
              <span>الأرباح</span>
              <span className="font-bold">0 ريال</span>
            </div>
            <div className="flex justify-between">
              <span>المدفوعات المعلقة</span>
              <span className="font-bold">0 ريال</span>
            </div>
          </div>
        </div>
      </div>

      {/* قسم إدارة المستخدمين */}
      <div className="mt-8">
        <div className="flex justify-between items-center mb-4">
          <h2 className="text-2xl font-bold">إدارة المستخدمين</h2>
          <button className="bg-primary-600 text-white px-4 py-2 rounded-md hover:bg-primary-700">
            إضافة مستخدم جديد
          </button>
        </div>
        <div className="bg-white rounded-lg shadow">
          <div className="p-6">
            <p className="text-gray-500 text-center">لا يوجد مستخدمين حالياً</p>
          </div>
        </div>
      </div>

      {/* قسم إدارة الخدمات */}
      <div className="mt-8">
        <div className="flex justify-between items-center mb-4">
          <h2 className="text-2xl font-bold">إدارة الخدمات</h2>
          <button className="bg-primary-600 text-white px-4 py-2 rounded-md hover:bg-primary-700">
            إضافة خدمة جديدة
          </button>
        </div>
        <div className="bg-white rounded-lg shadow">
          <div className="p-6">
            <p className="text-gray-500 text-center">لا توجد خدمات حالياً</p>
          </div>
        </div>
      </div>

      {/* قسم التقارير */}
      <div className="mt-8">
        <h2 className="text-2xl font-bold mb-4">التقارير</h2>
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          <div className="bg-white rounded-lg shadow p-6">
            <h3 className="text-lg font-semibold mb-4">تقرير المبيعات</h3>
            <div className="h-48 flex items-center justify-center">
              <p className="text-gray-500">لا توجد بيانات</p>
            </div>
          </div>
          <div className="bg-white rounded-lg shadow p-6">
            <h3 className="text-lg font-semibold mb-4">تقرير المستخدمين</h3>
            <div className="h-48 flex items-center justify-center">
              <p className="text-gray-500">لا توجد بيانات</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
} 